  content: '';
  position: absolute;
  left: 0;
  top: 0;
  z-index: -1;
  width: 100%;
  height: 60px;
  background: #84D444; }

.calendar-block .row-item {
  border-bottom: 1px solid #DFDFDF; }
  .calendar-block .row-item:first-child {
    border-top: 1px solid #DFDFDF; }

.calendar-block .item.date {
  height: 60px;
  font-size: 20px;
  font-weight: bold;
  line-height: 24px;
  text-transform: uppercase;
  color: #333333;
  border-right: 1px solid #DFDFDF; }
  .calendar-block .item.date:first-child {
    border-left: 1px solid #DFDFDF; }

